import React, { Component } from 'react'
import mypic from '../images/me.png'
class Popup extends Component{
    render(){
        return(
            <div className='popup_tot'>
                <div className='contact_me'>
                    <img src={mypic} alt="내사진" />
                    <p></p>
                </div>
            </div>
        )
    }
}

export default Popup;